@media screen and (max-width: 600px){
	.navBar {
		color: rgb(218, 195, 237);
		display: flex;
		flex-direction: row;
		justify-content: center;
		background-color: rgba(111, 28, 156, 0.1);
		border-bottom-left-radius: 2px;
		border-bottom-right-radius: 2px;
	}

	.btn {
		height: 25px;
		text-align: center;
		width: 80px;
		margin-left: 3px;
		margin-right: 3px;
		font-size: 15px;
	}
}